import React from 'react';
import style from './BurgerMenu.module.scss';
import burger_svg from './img/line-menu.svg';
import LeftMobbileMenu from './left-mobile-menu/LeftMobileMenu';


class BurgerMenu extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showMobMenu: false,
        }
    }
    toggleMobMenu = () => {
        this.setState({ showMobMenu: !this.state.showMobMenu });
    }
    render() {
        return <div className={style.mobMenu}>
            <img src={burger_svg}
                onClick={this.toggleMobMenu} />
            {this.state.showMobMenu && <LeftMobbileMenu toggleMobMenu={this.toggleMobMenu} />}
        </div>
    }
}

export default BurgerMenu;