import React from 'react';
import './App.scss';
import Header from '../header/Header';
import Info from '../main/banner/Info';
import AboutMe from '../main/aboutMe/AboutMe';
import Relationship from '../main/relationship/Relationship';
import Requirements from '../main/requirements/Requirements';
import Users from '../main/users/Users';
import Registration from '../main/registration/Registration';
import Footer from '../footer/Footer';
import * as Scroll from 'react-scroll';

class App extends React.Component {
  render() {

    let Element = Scroll.Element;

    return <>
      <Header />
      <main>
        <Info bannerSection={this.props.state.bannerSection} />
        <Element id="aboutMe" name="aboutMe" >
          <AboutMe aboutMeSection={this.props.state.aboutMeSection} />
        </Element>
        <Element id="relationships" name="relationships" >
          <Relationship relationshipSection={this.props.state.relationshipSection} />
        </Element>
        <Element id="requirements" name="requirements" >
          <Requirements requirementsSection={this.props.state.requirementsSection} />
        </Element>
        <Element id="users" name="users" >
          <Users usersSection={this.props.state.usersSection} />
        </Element>
        <Element id='signUp'>
          <Registration registrationSection={this.props.state.registrationSection} />
        </Element>
      </main>
      <Footer />
    </>
  }
}


export default App;
