import React from 'react';
import style from './Users.module.scss';
import axios from 'axios';

const baseUrl = 'https://frontend-test-assignment-api.abz.agency/api/v1';

class Users extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            itemsToshow: 6,
            page: '',
            total_pages: '',
            total_users: '',
            count: '',
            buttonDisabled: false,
            links: {
                next_url: null,
                prev_url: null
            },
        };
    };

    componentDidMount = () => {
        axios.get(
            `${baseUrl}/users?page=${this.state.page}&count=100`)
            .then(res => {
                console.log('users', res);
                if (res.data.success) {
                    this.setState({ users: res.data.users }); // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º –≤ —Å—Ç–µ–π—Ç
                }
            })
            .catch(err => { // –≤–æ–∑–º–æ–∂–Ω–æ–µ –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏–µ –æ—à–∏–±–æ–∫ 
                console.log('err', err);
            })
    };

    showMoreNext = () => {
        this.setState(prevState => ({ itemsToshow: prevState.itemsToshow + 6 }))
    };

    render() {
        return <section className={style.users}>
            <div className={style.users__content}>
                <div className={style.users__head}>
                    <h2>{this.props.usersSection.h2}</h2>
                    <h4>{this.props.usersSection.h4}</h4>
                </div>
                <div className={style.users__items_row}>
                    {this.state.users.slice(0, this.state.itemsToshow).map(u => <div key={u.id} className={style.item}>
                        <div className={style.item_img}>
                            <img src={u.photo} alt="avatar" />
                        </div>
                        <div className={style.item_description}>
                            <h5>{u.name}</h5>
                            <div className={style.personal_data}>
                                <p className={style.position}>{u.position}</p>
                                <p className={style.email}>{u.email}</p>
                                <p className={style.phone}>{u.phone}</p>
                            </div>
                        </div>
                    </div>
                    )}
                </div>
                <div className={style.users__btn} >
                    <button
                        onClick={this.showMoreNext}
                        disabled={this.state.buttonDisabled}
                    >Show more</button>
                </div>
            </div>
        </section>
    }
}
export default Users