import React from 'react';
import Logo from '../common/Logo/Logo';
import NavMenu from '../common/navMenu/NavMenu';
import style from './Header.module.scss';
import UserLogin from './LoginUser/UserLogin';
import { useMediaQuery } from 'react-responsive';
import BurgerMenu from './burger-menu/BurgerMenu';

const Desktop = ({ children }) => {
    const isDesktop = useMediaQuery({ minWidth: 769, maxWidth: 2560 })
    return isDesktop ? children : null
}
const Mobile = ({ children }) => {
    const isMobile = useMediaQuery({ minWidth: 320, maxWidth: 768 })
    return isMobile ? children : null
}

class Header extends React.Component {
    render() {
        return <header className={style.header}>
            <div className={style.header__content}>
                <Logo />
                <Desktop className={style.header__mobile_none}>
                    <NavMenu />
                    <div className={style.headerUserLogin}>
                        <UserLogin />
                    </div>
                </Desktop>
                <Mobile>
                    <BurgerMenu />
                </Mobile>
            </div>
        </header>
    }
}

export default Header
