import React from 'react';
import style from './Info.module.scss';
import { Link } from 'react-scroll';

const Info = (props) => {
    return <section className={style.bg_section}>
        <div className={style.content__info}>
            <h1 className={style.info__head}>{props.bannerSection.h1}</h1>
            <p className={style.info__text}>{props.bannerSection.info__text}
                <span className={style.info__text_span}>{props.bannerSection.info__text_span}</span>
            </p>
            <div className={style.info_btn}>
                <Link to='signUp' spy={true} smooth={true} offset={-100}>
                    <button className={style.info__btn}>Sign Up</button>
                </Link>
            </div>
        </div>
    </section>
}

export default Info;