import React from 'react';
import style from './Footer.module.scss';
import Logo from '../common/Logo/Logo';
import NavMenu from '../common/navMenu/NavMenu';
import Contacts from './contacts/Contacts';
import NavLinkPage from './navLinkPage/NavLinkPage';
import Social from './social/Social';

class Footer extends React.Component {
    render() {
        let nav = {
            News: '#',
            Overview: '#',
            Tutorials: '#',
            FAQ: '#',
            Blog: '#',
            Design: '#',
            Resources: '#',
            Terms: '#',
            Partners: '#',
            Code: '#',
            Guides: '#',
            Conditions: '#',
            Shop: '#',
            Collaborate: '#',
            Examples: '#',
            Help: '#'
        }
        return <footer className={style.footer}>
            <div className={style.footer_content}>
                <div className={style.wrapper_menu}>
                    <div className={style.wrapper_menu__invert}>
                        <div className={style.footer_logo}>
                            <Logo />
                        </div>
                        <div className={style.footer_navMenu}>
                            <NavMenu />
                        </div>
                    </div>
                </div>
                <div id={style.footer_line}></div>
                <div className={style.footer_info}>
                    <Contacts />
                    <NavLinkPage nav={nav} />
                </div>
                <div className={style.footer_bottom}>
                    <div className={style.footer_bottom__left}>
                        <p>¬© abz.agency specially for the test task</p>
                    </div>
                    <Social />
                </div>
            </div>
        </footer>
    }
}

export default Footer