import React from 'react';
import style from './SignUpForm.module.scss';
import Popup from './popup/Popup';
import axios from 'axios';

const baseUrl = 'https://frontend-test-assignment-api.abz.agency/api/v1';

class SignUpForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            formData: {
                name: '',
                nameError: '',
                email: '',
                emailError: '',
                phone: '',
                phoneError: '',
                position_id: null,
                photo: null,
            },
            btnValid: true,
            positions: [],
            showPopup: false,
            token: ''
        };
    }

    componentDidMount() {
        axios.get(`${baseUrl}/positions`)// –ø–æ–ª—É—á–∞–µ–º positions
            .then(res => {
                this.setState({ positions: res.data.positions })
            });
        this.getToken();
    };

    getToken = () => {
        axios.get(`${baseUrl}/token`)// –ø–æ–ª—É—á–∞–µ–º token
            .then(res => {
                console.log('token', res);
                if (res.data.success) {
                    this.setState({ token: res.data.token }); // –∑–∞–ø–∏—Å—ã–≤–∞–µ–º token –≤ —Å—Ç–µ–π—Ç
                }
            })
            .catch(err => {
                console.log('err', err);
            })
    };

    handleChange = e => {
        const { formData } = this.state;
        formData[e.target.name] = e.target.value;
        this.setState({ formData });
    };

    onChangeFile = e => {
        const { formData } = this.state;
        formData.photo = e.target.files[0]
        this.setState({ formData });
    };

    formLoad = () => {
        const token = this.state.token;
        const url = `${baseUrl}/users`;
        const formData = new FormData();
        formData.append('name', this.state.formData.name);
        formData.append('email', this.state.formData.email);
        formData.append('phone', this.state.formData.phone);
        formData.append('position_id', this.state.formData.position_id);
        formData.append('photo', this.state.formData.photo);
        const config = { headers: { 'Token': token } };
        return axios.post(url, formData, config)
            .then((res) => {
                if (res.data.status === 201) {
                    console.log('get', res)
                    this.setState({ showPopup: true })
                }
            })
    };

    handleSubmit = (e) => {
        e.preventDefault();
        this.formLoad()
        console.log("validate", this.state)
    };

    togglePopup = () => {
        this.setState({
            showPopup: !this.state.showPopup
        });
    };

    render() {
        return <div id={style.registration__form}>
            <form onSubmit={this.handleSubmit} className={style.form} noValidate>
                <div className={style.form__data}>
                    <div className={style.form__data_name}>
                        <fieldset>
                            <legend>Name</legend>
                            <input
                                onChange={this.handleChange}
                                type="text"
                                placeholder="Your name"
                                name="name"
                            />
                        </fieldset>
                        <div>{this.state.formData.nameError}</div>
                    </div>
                    <div className={style.form__data_email}>
                        <fieldset>
                            <legend>Email</legend>
                            <input
                                onChange={this.handleChange}
                                type="email"
                                placeholder="Your email"
                                name="email"
                            />
                        </fieldset>
                        <div>{this.state.formData.emailError}</div>
                    </div>
                    <div className={style.form__data_phone}>
                        <fieldset>
                            <legend>Phone</legend>
                            <input
                                onChange={this.handleChange}
                                type="phone"
                                placeholder="+38 (___) ___ __ __"
                                name="phone"
                            />
                        </fieldset>
                        <div>{this.state.formData.phoneError}</div>
                    </div>
                </div>
                <div className={style.formInstruction__select}>
                    <fieldset className={style.select}>
                        <select
                            onChange={this.handleChange}
                            name="position_id">
                            <option>Select your position</option>
                            {this.state.positions.map(p => <option key={p.id} value={p.id}>{p.name}</option>)}
                        </select>
                    </fieldset>
                    <div className={style.uploadPhoto}>
                        <fieldset>
                            <label>
                                <span>Upload yuor photo</span>
                                <input
                                    onChange={this.onChangeFile}
                                    type="file"
                                    accept="image/.jpeg, .jpg"
                                />
                                <strong>Upload</strong>
                            </label>
                        </fieldset>
                        <p className={style.uploadPhoto__description}>File format jpg up to 5MB, the minimum size of 70x70px</p>
                    </div>
                </div>
                <div className={style.btnSignUp}>
                    <button
                        type="submit"
                        onClick={this.togglePopup}
                        disabled={!this.state.btnValid}
                    >SignUp</button>
                    {this.state.showPopup && <Popup closePopup={this.togglePopup} />}
                </div>
            </form>
        </div>
    }
}

export default SignUpForm;
