import React from 'react';
import style from './NavMenu.module.scss';
import { Link } from 'react-scroll';
import { useMediaQuery } from 'react-responsive';

const Mobile = ({ children }) => {
    const isMobile = useMediaQuery({ minWidth: 320, maxWidth: 768 })
    return isMobile ? children : null
}

class NavMenu extends React.Component {
    render() {
        return <nav className={style.navMenu}>
            <ul>
                <li>
                    <Link to='aboutMe'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>About me</Link>
                </li>
                <li>
                    <Link to='relationships'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Relationships</Link>
                </li>
                <li>
                    <Link to='requirements'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={20}
                        className={style.header__navItem}>Requirements</Link>
                </li>
                <li>
                    <Link to='users'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Users</Link>
                </li>
                <li>
                    <Link to='signUp'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Sign Up</Link>
                </li>
            </ul>
        </nav >
    }
}

export default NavMenu
