import React from 'react';
import style from './Registration.module.scss';
import SignUpForm from './form/SignUpForm';


class Registration extends React.Component {

    render() {
        return <section className={style.registration}>
            <div className={style.registration__head}>
                <h2>{this.props.registrationSection.h2}</h2>
                <h4>{this.props.registrationSection.h4.after}<br className={style.breakPoint_4} />
                    {this.props.registrationSection.h4.before}
                </h4>
            </div>
            <SignUpForm />
        </section>
    }
}

export default Registration;
