import React from 'react';
import style from './LeftMobileMenu.module.scss';
import UserLogin from '../../LoginUser/UserLogin';
import NavMeu from '../../../common/navMenu/NavMenu';
import MobileMenu from '../../../common/mobileMenu/MobileMenu';


class LeftMobileMenu extends React.Component {
    render() {
        return <div onClick={this.props.toggleMobMenu} className={style.mobMenu__conteiner}>
            <div className={style.conteiner__row}>
                <UserLogin />
                <MobileMenu />
            </div>
        </div>
    }
}

export default LeftMobileMenu;