import React from 'react';
import style from './NavLinkPage.module.scss';


class NavLinkPage extends React.Component {
    render() {
        let nav = this.props.nav;
        return <nav className={style.footer_navLink}>
            <ul className={style.navLink_items}>
                {Object.keys(nav).map((elem, index) =>
                    <li
                        className={style.item}
                        key={index}>
                        <a href={nav[elem]}>{elem}</a>
                    </li>
                )}
            </ul>
        </nav>
    }
}

export default NavLinkPage;