import React from 'react';
import style from './AboutMe.module.scss';
import man_mobile from './img/man-mobile.svg';
import { Link } from 'react-scroll';


const AboutMe = (props) => {
    return <section className={style.aboutMe}>
        <div className={style.aboutMe__head}>
            <h2>{props.aboutMeSection.h2}</h2>
        </div>
        <div className={style.aboutMe__content}>
            <div className={style.aboutMe__img}>
                <img src={man_mobile} alt="man-mobile" />
            </div>
            <div className={style.aboutMe__description}>
                <h3>{props.aboutMeSection.h3}</h3>
                <div className={style.description__text}>
                    <p className={style.text__g—Éneral}>{props.aboutMeSection.text__g—Éneral}</p>
                    <p className={style.text__overflow}>{props.aboutMeSection.text__overflow}</p>
                </div>
                <div className={style.aboutMe__btn}>
                    <Link to='signUp' spy={true} smooth={true} offset={-100}>Sign Up</Link>
                </div>
            </div>
        </div>
    </section>
}

export default AboutMe
