import React from 'react';
import style from './Relationship.module.scss';
import html from './img/html.svg';
import css from './img/css.svg';
import js from './img/javascript.svg';


const Relationship = (props) => {
    return <section className={style.relationship}>
        <div className={style.relationship__head}>
            <h2>{props.relationshipSection.h2}</h2>
        </div>
        <div className={style.relationship__content}>
            <div className={style.description__card__html}>
                <img src={html} alt="my-favorite-html" />
                <div className={style.description__html}>
                    <h3>{props.relationshipSection.html_h3}</h3>
                    <p>{props.relationshipSection.description__html_1}<br className={style.breakPoint_1} />
                        {props.relationshipSection.description__html_2}<br className={style.breakPoint_2} />
                        {props.relationshipSection.description__html_3}<br className={style.breakPoint_3} />
                        {props.relationshipSection.description__html_4}
                    </p>
                </div>
            </div>
            <div className={style.description__card__css}>
                <img src={css} alt="my-favorite-css" />
                <div className={style.description__css}>
                    <h3>{props.relationshipSection.css_h3}</h3>
                    <p>{props.relationshipSection.description__css}</p>
                </div>
            </div>
            <div className={style.description__card__js}>
                <img src={js} alt="my-favorite-js" />
                <div className={style.description__js}>
                    <h3>{props.relationshipSection.js_h3}</h3>
                    <p>{props.relationshipSection.description__js}</p>
                </div>
            </div>
        </div>
    </section>
}

export default Relationship