import React from 'react';
import style from './Requirements.module.scss';

const Requirements = (props) => {
    return <section className={style.requirements}>
        <div className={style.requirements__bg}>
            <div className={style.requirements__content}>
                <div className={style.requirements__head}>
                    <h2>{props.requirementsSection.h2}</h2>
                </div>
                <div className={style.requirements_content__description}>
                    <div className={style.description__text}>
                        <p className={style.users_text}>{props.requirementsSection.description__text_users_text}</p>
                        <p className={style.developer__working}>{props.requirementsSection.description__text_developer__working_1}<br />
                            {props.requirementsSection.description__text_developer__working_2}</p>
                        <p className={style.js_text}>{props.requirementsSection.description__text_js_text}</p>
                    </div>
                    <div className={style.description__img}>
                    </div>
                </div>
            </div>
        </div>
    </section>
}

export default Requirements
