import React from 'react';
import style from './MobileMenu.module.scss';
import { Link } from 'react-scroll';


class MobileMenu extends React.Component {
    render() {
        return <nav className={style.MobileMenu}>
            <ul>
                <li>
                    <Link to='aboutMe'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>About me</Link>
                </li>
                <li>
                    <Link to='relationships'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Relationships</Link>
                </li>
                <li>
                    <Link to='requirements'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={20}
                        className={style.header__navItem}>Requirements</Link>
                </li>
                <li>
                    <Link to='users'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Users</Link>
                </li>
                <li>
                    <Link to='signUp'
                        activeClass={style.activeLink}
                        spy={true} smooth={true}
                        offset={-100}
                        className={style.header__navItem}>Sign Up</Link>
                </li>
                <li>
                    <Link to='signOut'
                        activeClass={style.activeLink}
                        className={style.header__navItem}>Sign Out</Link>
                </li>
            </ul>
        </nav >
    }
}

export default MobileMenu
